#emptyPage {
  .navbar {
    background: #348d8f;
    background: var(--elife-green-1);
  }

  .updateTime {
    font-size: .8rem
  }

  .tabnav-container {
    background: var(--elife-green-1);;
  }

  .ant-tabs-nav-wrap::before {
    display: none;
  }
  .ant-tabs-nav-wrap::after {
    display: none;
  }
  .ant-tabs-nav-wrap {
    width: 100%;
  }

  .ant-tabs-nav {
    margin: 0;
    .ant-tabs-tab-active {
      background: var(--elife-green-1) !important;
      .tabText {
        color: #056608;
        background: white;
        font-size: 1.2rem;
        font-weight: 600;
      }
    }

    .ant-tabs-tab {
      padding: 6px 1px;
      margin-left: 3px;
    }

    .tabText {
      color: white;
      font-size: 1.15rem;
      font-weight: 600;
      width: 90px;
      text-align: center;
      border-radius: 8px 8px 0 0;
      border: 1px solid #f0f0f0;
      padding: 10px 5px 5px 5px;
    }
  }

  .ant-tabs-nav::before {
    display: none !important;
  }

  .InsuranceEndButton {
    font-size: 1.15rem;
    width: 100px!important;
    border-radius: 10px;
  }

}
